ckground-color: #2987BB;\n  border-radius: 4px; }\n\n/*----------------------Sidebar-------------------*/\n\n.wrapper {\n  transition: all .4s ease 0s;\n  height: 100%; }\n\n#sidebar-wrapper {\n  margin-left: -150px;\n  left: 70px;\n  width: 200px;\n  background: #2D4161;\n  position: fixed;\n  height: 100%;\n  z-index: 10000;\n  transition: all .4s ease 0s; }\n\n.sidebar-nav {\n  display: block;\n  float: left;\n  width: 200px;\n  list-style: none;\n  margin: 0;\n  padding: 0; }\n\n.wrapper {\n  padding-left: 0; }\n\n#wrapper.active #sidebar-wrapper {\n  left: 150px; }\n\n#sidebar_menu li a, .sidebar-nav li a {\n  color: white;\n  display: block;\n  float: none;\n  text-decoration: none;\n  width: 200px;\n  background: #2D4161;\n  transition: background .5s; }\n\n#sidebar_menu li a, .sidebar-brand li a {\n  color: white;\n  display: block;\n  float: none;\n  text-decoration: none;\n  width: 200px;\n  background: #2987BB;\n  transition: background .5s; }\n\n.sidebar_name {\n  padding-top: 25px;\n  color: white;\n  opacity: .7; }\n\n.sidebar-nav li {\n  line-height: 50px;\n  text-indent: 30px; }\n\n.sidebar-nav li a {\n  color: white;\n  display: block;\n  text-decoration: none; }\n\n.sidebar-nav li a:hover {\n  color: black;\n  background: #2987BB;\n  text-decoration: none; }\n\n.sidebar-nav li a:active,\n.sidebar-nav li a:focus {\n  text-decoration: none; }\n\n.sidebar-nav > .sidebar-user {\n  height: 180px;\n  line-height: 60px;\n  font-size: 18px;\n  text-align: left; }\n\n.sidebar-nav > .sidebar-user a {\n  color: white; }\n\n.sidebar-nav > .sidebar-user a:hover {\n  color: white;\n  background: none; }\n\n.sidebar-nav > .sidebar-brand {\n  height: 71px;\n  line-height: 60px;\n  font-size: 20px;\n  font-weight: bold;\n  text-align: left;\n  background-color: #2987BB; }\n\n.sidebar-nav > .sidebar-brand a {\n  color: white; }\n\n.sidebar-nav > .sidebar-brand a:hover {\n  color: black;\n  background: none; }\n\n/*# sourceMappingURL=data:application/json;base64, */"